

import {login, register} from '@/api'
import XHeader from 'vux/src/components/x-header/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
components: {
XHeader,
Icon
},
name: 'login',
data () {
return {
userName: '',
password: '',
newName: '',
newPassword1: '',
newPassword2: '',
showLogin: true
}
},
methods: {
backspace () {
this.$router.go(-1)
},
userLogin () {
if (this.userName === '' || this.password === '') {
this.$message({
message: '',
type: 'warning',
center: true
})
} else {
var userVo = {
'name': this.userName,
'password': this.password
}
/*  */
login(userVo).then((response) => {
/*  (-1,),(-2,),(1,)  */
if (response.data.code === -1) {
this.$message({
message: '!',
type: 'error',
center: true
})
} else if (response.data.code === -2) {
this.$message({
message: '!',
type: 'error',
center: true
})
} else {
this.$message({
message: '!',
type: 'success',
center: true
})
localStorage.setItem('userName', this.userName)
this.$store.commit('setUserData', response.data.data)
setTimeout(function () {
this.$router.push({path: '/'})
this.$store.dispatch('login')
}.bind(this), 1000)
this.userName = ''
this.password = ''
}
})
}
},
UserRegister () {
if (this.newName === '' || this.newPassword1 === '') {
this.$message({
message: '',
type: 'warning',
center: true
})
} else if (this.newPassword2 === '') {
this.$message({
message: '',
type: 'warning',
center: true
})
} else if (this.newPassword1 !== this.newPassword2) {
this.$message({
message: '',
type: 'erorr',
center: true
})
} else {
let data = {
'name': this.newName,
'password': this.newPassword1
}
register(data).then((response) => {
if (response.data.message === 'ok') {
this.$message({
message: ',',
type: 'success',
center: true
})
this.newName = ''
this.newPassword1 = ''
this.newPassword2 = ''
/*  */
setTimeout(function () {
this.showLogin = true
}.bind(this), 1000)
}
})
}
}
}
}



